<template>
    <div>
        <Message :msg="msg" v-show="msg" />
        <div>
        <form id="burger-form" @submit="createBurger">

            <div class="input-container">
                <label for="nome">Nome do cliente:</label>
                <input type="text" id="nome" name="nome" v-model="nome" placeholder="Digite seu nome">
            </div>

            <div class="input-container">
                <label for="pao">Escolha o pão:</label>
                <select name="pao" id="pao" v-model="pao">
                    <option value="">Selecione o seu pão</option>
                    <option v-for="pao in paes" :key="pao.id" :value="pao.tipo">
                    {{pao.tipo}}
                    </option>
                </select>
            </div>

            <div class="input-container">
                <label for="carne">Escolha a carne do seu Burger:</label>
                <select name="carne" id="carne" v-model="carne">
                    <option value="">Selecione a sua carne</option>
                    <option v-for="carne in carnes" :key="carne.id" :value="carne.tipo">
                    {{carne.tipo}}
                    </option>
                </select>
            </div>

            <div id="opcionais-container" class="input-container">
                <label id="opcionais-title" for="opcionais">Selecione opcionais:</label>
                <div class="checkbox-container" v-for="opcional in opcionaisdata" :key="opcional.id">
                    <input type="checkbox" name="opcionais" v-model="opcionais" :value="opcional.tipo">
                    <span>{{opcional.tipo}}</span>
                </div>
            </div>

            <div class="input-container">
                <input type="submit" class="submit-btn" value="Criar meu Burger">
            </div>

        </form>
        </div>
    </div>
</template>

<script>
    import Message from './Message.vue';
    
    export default {
    name: "BurgerForm",
    data() {
        return {
            paes: null,
            carnes: null,
            opcionaisdata: null,
            nome: null,
            pao: null,
            carne: null,
            opcionais: [],
            status: "Solicitado",
            msg: null
        };
    },
    methods: {
        //metodo GET
        async getIngredientes() {
            const req = await fetch("http://localhost:3000/ingredientes");
            const data = await req.json();
            //console.log(data); para verificar o que chega no objeto
            this.paes = data.paes;
            this.carnes = data.carnes;
            this.opcionaisdata = data.opcionais;
        },
        //metodo CREATE
        async createBurger(e) {
            e.preventDefault(); //esse evento breca o formulario ao criar o hamburger.
            //console.log("Criou o hamburger") testar se ele para o evento do formulario no console.
            const data = {
                nome: this.nome,
                carne: this.carne,
                pao: this.pao,
                opcionais: Array.from(this.opcionais),
                status: "Solicitado"
            };
            //console.log(data);//verificar se esta pegando os dados do formulario e criando o Burger.
            const dataJson = JSON.stringify(data); // Transformar o Json em texto
            const req = await fetch("http://localhost:3000/burgers", {
                method: "POST",
                headers: { "Content-type": "application/json" },
                body: dataJson
            });
            const res = await req.json();
            //console.log(res);

            //Colocar uma msg de sistema
            this.msg = `Pedido Nº: ${res.id} realizado com sucesso.`

            //Limpar menssagem
            setTimeout(() => this.msg = "", 3000);

            //limpar os campos
            this.nome = "";
            this.carne = "";
            this.pao = "";
            this.opcionais = "";
        }
    },
    mounted() {
        this.getIngredientes(); //aqui eu chamo o metodo getIngredientes para que seja carregado os dados do banco
    },
    components: { Message }
}
</script>

<style scoped>
    #burger-form {
        max-width: 400px;
        margin: 0 auto;
    }

    .input-container {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
    }

    label {
        font-weight: bold;
        margin-bottom: 15px;
        color: #222;
        padding: 5px 10px;
        border-left: 4px solid #FBCA03;
    }

    input, select {
        padding: 5px 10px;
        width: 300px;
    }

    #opcionais-container {
        flex-direction: row;
        flex-wrap: wrap;
    }

    #opcionais-title{
        width: 100%;
    }

    .checkbox-container {
        display: flex;
        align-items: flex-start;
        width: 50%;
        margin-bottom: 20px;
    }

    .checkbox-container span,
    .checkbox-container input {
        width: auto;
    }

    .checkbox-container span {
        margin-left: 6px;
        font-weight: bold;
    }

    .submit-btn {
        background-color: #222;
        color: #FBCA03;
        font-weight: bold;
        border: 2px solid #222;
        padding: 10px;
        font-size: 16px;
        margin: 0 auto;
        cursor: pointer;
        transition: .5s;
    }

    .submit-btn:hover {
        background-color: transparent;
        color: #222;
    }

</style>